<template>
  <main
    class="bg-gradient-to-t from-teal-200 to-teal-500 flex flex-row items-center justify-center min-h-screen"
  >
    <Card class="sm:w-full sm:h-full md:w-1/2 md:h-1/2 lg:w-1/3 lg:h-1/3">
      <template #title>
        <h2 class="text-center pb-4">Login</h2>
      </template>
      <template #content>
        <div class="flex flex-col gap-2">
          <div class="flex flex-row gap-2 items-center">
            <li class="pi pi-user" />
            <InputText
              type="email"
              v-model:model-value="formData.mail"
              placeholder="email"
              class="w-full"
            >
            </InputText>
          </div>
          <div class="flex flex-row gap-2 items-center">
            <li class="pi pi-key" />
            <InputText
              v-model:model-value="formData.password"
              placeholder="password"
              class="w-full"
              type="password"
            >
            </InputText>
          </div>
        </div>
      </template>
      <template #footer>
        <div class="flex flex-col gap-2">
          <Button @click.prevent="handleLogin"> Login </Button>
          <router-link
            class="flex flex-row-reverse w-full text-teal-500 font-bold hover:cursor-pointer"
            replace
            :to="{ name: 'register', params: { username: 'erina' } }"
          >
            Register here!
          </router-link>
        </div>
      </template>
    </Card>
  </main>
</template>

<script setup lang="ts">
import Card from "primevue/card";
import InputText from "primevue/inputtext";
import Button from "primevue/button";
import router from "@/router";
import { login } from "@/services/auth";
import { ref } from "vue";
import senNotification from "@/services/push-not";

const formData = ref({
  mail: "",
  password: "",
});

const handleLogin = async () => {
  try {
    if (formData.value.mail === "" || formData.value.password === "") {
      senNotification("error", "Please fill all the fields");
      return;
    }
    console.log(formData.value);
    const user = await login(formData.value.mail, formData.value.password);

    localStorage.setItem("user", JSON.stringify(user));

    router.push({ name: "home" });
  } catch (error) {
    senNotification("error", "Login failed");
  }
};
</script>
